import { DomainEventHandler } from './domain-event.handler';
import { AggregateId } from '../../tactical/aggregate/aggregate-id';
import { DomainEvent } from '../domain-event';
import { MultiDomainEventHandler } from './multi-domain-event.handler';
import { DomainEventType } from './domain-event-type';


export function domainEventHandlerFactory<I extends AggregateId, E extends DomainEvent<I>>(
	domainEventHandler: DomainEventHandler<I, E>
): DomainEventHandlerImpl<I, E> {
	return new DomainEventHandlerImpl<I, E>(domainEventHandler, [domainEventHandler.forEvent()]);
}


export function multiDomainEventHandlerFactory<I extends AggregateId, E extends DomainEvent<I>>(
	domainEventHandler: MultiDomainEventHandler<I, E>
): DomainEventHandlerImpl<I, E> {
	return new DomainEventHandlerImpl<I, E>(domainEventHandler, domainEventHandler.forEvents());
}


export class DomainEventHandlerImpl<I extends AggregateId, E extends DomainEvent<I>> {

	private readonly eventTypes: Array<string>;

	constructor(private readonly domainEventHandler: DomainEventHandler<I, E> | MultiDomainEventHandler<I, E>,
				private readonly events: Array<DomainEventType<E>>) {
		this.eventTypes = this.createDomainEventTypes();
	}

	handleEvent(event: E): void {
		this.domainEventHandler.handle(event);
	}

	forEvents(events: Array<E>): boolean {
		return events.some((event: E) => {
			return this.eventTypes.some((type: string) => {
				return type === event.getMessageType();
			});
		});
	}

	private createDomainEventTypes(): Array<string> {

		let types = [];

		for (let event of this.events) {
			let instance = this.createDomainEventInstance(event);
			types.push(instance.getMessageType());
		}

		return types;
	}

	private createDomainEventInstance(eventType: DomainEventType<E>): E {
		const args: Array<void> = [],
			argumentLength = eventType.constructor.length;

		args.fill(undefined, 0, argumentLength);

		return (new (eventType as any)(...args));
	}

}
