import { Key } from './key';
import { Optional } from '../optional';

export class KeyMap<K extends Key, T> {

	private readonly keys = new Map<string, K>();

	private readonly values = new WeakMap<K, T>();

	get(key: K): Optional<T> {

		const internalKey = this.getInternalKey(key);

		if (internalKey) {
			return Optional.of(this.values.get(internalKey));
		} else {
			return Optional.empty();
		}
	}

	has(key: K): boolean {

		const internalKey = this.getInternalKey(key);

		return this.values.has(internalKey);
	}

	set(key: K, value: T): void {

		const internalKey = this.getInternalKey(key);

		this.values.set(internalKey, value);
	}

	private getInternalKey(key: K): K {

		const realKey = this.keys.get(key.toString());

		if (!realKey) {
			this.keys.set(key.toString(), key);
			return key;
		} else {
			return realKey;
		}
	}

}
