import { StaticProvider, Type } from '@angular/core';

import { DomainEvent } from './event/domain-event';

import { AggregateId } from './tactical/aggregate/aggregate-id';
import { DomainEventHandler } from './event/handler/domain-event.handler';
import { DOMAIN_EVENT_HANDLERS } from './event/handler/domain-event-handlers';

export function provideEventHandlers<I extends AggregateId, E extends DomainEvent<I>>(handlers: Array<Type<DomainEventHandler<I, E>>>): Array<StaticProvider> {
	return handlers.map((handler: Type<DomainEventHandler<I, E>>) => {
		return {
			provide: DOMAIN_EVENT_HANDLERS,
			useClass: handler,
			multi: true
		};
	}) as any;
}
