import {
	createAggregateCommandHandlerFactory,
	CreateAggregateCommandHandlerImpl
} from '../domain/command/create-aggregate/create-aggregate.command-handler-impl';
import { CREATE_AGGREGATE_COMMAND_HANDLERS } from '../domain/command/create-aggregate/create-aggregate.command-handlers-token';
import { AggregateFactoryArchive, AggregateRepositoryArchive } from '../domain/command/config/define';
import { AggregateFactory } from '../domain/tactical/aggregate/aggregate-factory';
import { aggregateDefinitionToken } from '../domain/command/config/aggregate-definition.token';
import { AggregateDefinition } from '../domain/command/config/aggregate-definition';
import { AggregateRepository } from '../domain/tactical/aggregate/aggregate-repository';
