import { HermesDomainModule } from './hermes.domain.module';
import { COMMAND_HANDLERS } from '../domain/command/handler/command-handlers';
import { commandHandlerFactory, CommandHandlerImpl } from '../domain/command/handler/command-handler-impl';
import { CommandHandler } from '../domain/command/handler/command.handler';
import { AggregateId } from '../domain/tactical/aggregate/aggregate-id';
