import { DomainEventHandler } from '../domain/event/handler/domain-event.handler';
import { MultiDomainEventHandler } from '../domain/event/handler/multi-domain-event.handler';


const hermesProviders = [
	RandomStringGenerator,
	{ provide: FILTERED_COMMAND_STREAM, useExisting: CommandStream },
	CommandBus,
	CommandStream,
	CommandDispatcher,
	AggregateStoreRegister,

	DomainEventBus,
	DomainEventStream,
	DomainEventPublisher,
	DomainEventStore,

	HermesApi
];

const providers = [
	{ provide: EVENT_LOGGER_ENABLED, useValue: true },
	{ provide: COMMAND_LOGGER_ENABLED, useValue: true },
	{ provide: CommandLogger, useFactory: commandLoggerFactory, deps: [COMMAND_LOGGER_ENABLED, ConsoleCommandLogger, NoopCommandLogger] },
	{ provide: DomainEventLogger, useFactory: eventLoggerFactory, deps: [EVENT_LOGGER_ENABLED, ConsoleEventLogger, NoopEventLogger] },
	ConsoleCommandLogger,
	NoopCommandLogger,
	NoopEventLogger,
	ConsoleEventLogger,
	HermesLoggersInitializer,
	AggregateFactoryArchive,
	AggregateRepositoryArchive,
	...hermesProviders
];

export function commandLoggerFactory(enabled: boolean, consoleCommandLogger: ConsoleCommandLogger, noopCommandLogger: NoopCommandLogger) {

	if (enabled) {
		return consoleCommandLogger;
	} else {
		return noopCommandLogger;
	}
}

export function eventLoggerFactory(enabled: boolean, consoleEventLogger: ConsoleEventLogger, noopEventLogger: NoopEventLogger) {

	if (enabled) {
		return consoleEventLogger;
	} else {
		return noopEventLogger;
	}
}

export class HermesBaseModule<I extends AggregateId, A extends AggregateRoot<I>, C extends Command<I>, E extends DomainEvent<I>> extends Reactive implements OnDestroy {

	constructor(@Optional() @Inject(DOMAIN_EVENT_HANDLERS) eventHandlers: Array<DomainEventHandlerImpl<I, E>>,
				@Optional() @Inject(CREATE_AGGREGATE_COMMAND_HANDLERS) aggregateCommandHandlers: Array<CreateAggregateCommandHandlerImpl<I, A, C>>,
				@Optional() @Inject(COMMAND_HANDLERS) commandHandlers: Array<CommandHandlerImpl<I, A, C>>,
				@Optional() @Inject(aggregateDefinitionToken) definedAggregate: Array<AggregateDefinition<I, A>>,
				injector: Injector,
				aggregateFactoryArchive: AggregateFactoryArchive<I, A>,
				aggregateRepositoryArchive: AggregateRepositoryArchive<I, A>,
				commandBus: CommandBus,
				domainEventBus: DomainEventBus,
				private hermesLoggersInitializer: HermesLoggersInitializer,
				private hermesApi: HermesApi) { // HermesApi initialization
		super();

		this.hermesLoggersInitializer.start();

		this.checkNullCommand(commandBus, commandHandlers, aggregateCommandHandlers);
		this.checkCommandHandlerIsCollection(commandHandlers);
		this.checkDomainEventHandlerIsCollection(eventHandlers);

		if (definedAggregate) {
			definedAggregate.forEach((def: AggregateDefinition<I, A>) => {

				const factory = injector.get(def.factory),
					repository = injector.get(def.repository);

				aggregateFactoryArchive.add(def.key, factory);
				aggregateRepositoryArchive.add(def.key, repository);
			});
		}

		if (aggregateCommandHandlers) {

			aggregateCommandHandlers.forEach((handler: CreateAggregateCommandHandlerImpl<I, A, C>) => {
				commandBus
					.ofCreateAggregateHandler(handler)
					.pipe(this.takeUntil())
					.subscribe((command: C) => {
						handler.handleCommand(command);
					});
			});
		}

		if (commandHandlers) {
			commandHandlers.forEach((handler: CommandHandlerImpl<I, A, C>) => {
				commandBus
					.ofCommandHandler(handler)
					.pipe(this.takeUntil())
					.subscribe((command: C) => {
						handler.handleCommand(command);
					});
			});
		}

		if (eventHandlers) {

			eventHandlers.forEach((handler: DomainEventHandlerImpl<I, E>) => {
				domainEventBus
					.ofEventHandler(handler)
					.pipe(this.takeUntil())
					.subscribe((event: E) => {
						handler.handleEvent(event);
					});
			});

			// domainEventBus
			// 	.pipe(this.takeUntil())
			// 	.subscribe((event: E) => {
			//
			// 		eventHandlers.forEach((handler: DomainEventHandlerImpl<I, E>) => {
			// 			handler.handleEvent(event);
			// 		});
			// 	});
		}
	}

	ngOnDestroy() {
		super.ngOnDestroy();
		this.hermesLoggersInitializer.stop();
	}

	private checkNullCommand(commandBus: CommandBus, commandHandlers: Array<CommandHandlerImpl<I, A, C>>, aggregateCommandHandlers: Array<CreateAggregateCommandHandlerImpl<I, A, C>>): void {
		commandBus
			.ofNullHandler(commandHandlers, aggregateCommandHandlers)
			.pipe(this.takeUntil())
			.subscribe((command: C) => {
				console.error(`Command ${command.toString()} was not intercepted by any CommandHandler.`);
			});
	}

	private checkCommandHandlerIsCollection(commandHandlers: Array<CommandHandlerImpl<I, A, C>>): void {
		if (commandHandlers && !Array.isArray(commandHandlers)) {
			console.warn(`You might provided commandHandler without specifying "multi: true".`);
		}
	}

	private checkDomainEventHandlerIsCollection(eventHandlers: Array<DomainEventHandlerImpl<I, E>>): void {
		if (eventHandlers && !Array.isArray(eventHandlers)) {
			console.warn(`You might provided eventHandler without specifying "multi: true".`);
		}
	}

}

@NgModule({
	imports: [
		CommonModule
	],
	providers: providers
})
export class HermesModule extends HermesBaseModule<any, any, any, any> {

	static defineAggregate<I extends AggregateId, A extends AggregateRoot<I>, C extends Command<I>>(aggregateKey: string,
																								 factory: Type<AggregateFactory<I, A>>,
																								 repository: Type<AggregateRepository<I, A>>,
																								 createHandler: Type<CreateAggregateCommandHandler<A, C>>,
																								 commandHandlers: Array<Provider> = [],
																								 domainEventHandlers: Array<Provider> = []): ModuleWithProviders<any> {
		return {
			ngModule: HermesDomainModule,
			providers: [{
				provide: aggregateDefinitionToken,
				multi: true,
				useValue: {
					key: aggregateKey,
					factory: factory,
					repository: repository
				}
			}, {
				provide: aggregateKey,
				useValue: aggregateKey
			},
				factory,
				repository,
				...HermesModule.registerCreateCommandHandler(createHandler, aggregateKey),
				...commandHandlers,
				...domainEventHandlers
			]
		};
	}

	static withConfig(config: HermesModuleConfig = { loggers: false }): ModuleWithProviders<HermesModule> {
		return {
			ngModule: HermesModule,
			providers: providers
		};
	}

	static registerCommandHandler<I extends AggregateId, A extends AggregateRoot<I>, C extends Command<I>>(
		commandHandlerType: Type<CommandHandler<A, C>>,
		aggregateName: string): Array<Provider> {

		return [
			{
				provide: commandHandlerType,
				useClass: commandHandlerType
			}, {
				provide: COMMAND_HANDLERS,
				useFactory: commandHandlerFactory,
				multi: true,
				deps: [
					commandHandlerType,
					AggregateRepositoryArchive,
					aggregateName
				]
			}];
	}

	static registerDomainEventHandler<I extends AggregateId, E extends DomainEvent<I>>(
		domainEventHandlerType: Type<DomainEventHandler<I, E>>
	): Array<Provider> {

		return [
			{
				provide: domainEventHandlerType,
				useClass: domainEventHandlerType
			}, {
				provide: DOMAIN_EVENT_HANDLERS,
				useFactory: domainEventHandlerFactory,
				multi: true,
				deps: [
					domainEventHandlerType
				]
			}];
	}

	static registerMultiDomainEventHandler<I extends AggregateId, E extends DomainEvent<I>>(
		domainEventHandlerType: Type<MultiDomainEventHandler<I, E>>
	): Array<Provider> {

		return [
			{
				provide: domainEventHandlerType,
				useClass: domainEventHandlerType
			}, {
				provide: DOMAIN_EVENT_HANDLERS,
				useFactory: multiDomainEventHandlerFactory,
				multi: true,
				deps: [
					domainEventHandlerType
				]
			}];
	}

	constructor(@Optional() @Inject(DOMAIN_EVENT_HANDLERS) eventHandlers: Array<DomainEventHandlerImpl<any, any>>,
				@Optional() @Inject(CREATE_AGGREGATE_COMMAND_HANDLERS) aggregateCommandHandlers: Array<CreateAggregateCommandHandlerImpl<any, any, any>>,
				@Optional() @Inject(COMMAND_HANDLERS) commandHandlers: Array<CommandHandlerImpl<any, any, any>>,
				@Optional() @Inject(aggregateDefinitionToken) definedAggregate: Array<AggregateDefinition<any, any>>,
				injector: Injector,
				aggregateFactoryArchive: AggregateFactoryArchive<any, any>,
				aggregateRepositoryArchive: AggregateRepositoryArchive<any, any>,
				commandBus: CommandBus,
				domainEventBus: DomainEventBus,
				hermesLoggersInitializer: HermesLoggersInitializer,
				hermesApi: HermesApi) { // HermesApi initialization
		super(
			eventHandlers,
			aggregateCommandHandlers,
			commandHandlers,
			definedAggregate,
			injector,
			aggregateFactoryArchive,
			aggregateRepositoryArchive,
			commandBus,
			domainEventBus,
			hermesLoggersInitializer,
			hermesApi
		);
	}

	private static registerCreateCommandHandler<I extends AggregateId, A extends AggregateRoot<I>, C extends Command<I>>(
		createCommandHandlerType: Type<CreateAggregateCommandHandler<A, C>>,
		aggregateName: string): Array<Provider> {

		return [
			{
				provide: createCommandHandlerType,
				useClass: createCommandHandlerType
			}, {
				provide: CREATE_AGGREGATE_COMMAND_HANDLERS,
				useFactory: createAggregateCommandHandlerFactory,
				multi: true,
				deps: [
					createCommandHandlerType,
					AggregateFactoryArchive,
					AggregateRepositoryArchive,
					aggregateName,
					DomainEventPublisher
				]
			}
		];
	}

}
